.social-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  border: 1px solid $text-color;
  border-radius: 50%;
  transition: all $transition-time;

  svg {
    fill: currentColor;
    transition: fill $transition-time;
  }
}
