//        - gallery-photo,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.gallery-photo {

  $block-name:                &; // #{$block-name}__element {}

  margin-top: 30px;

  &__inner {
    display: flex;
    flex-wrap: wrap;

    @media (min-width: $screen-md) {
      margin-left: -15px;
      margin-right: -15px;
    }
  }

  &__item {
    width: 100%;
    margin-bottom: 25px;

    @media (min-width: $screen-md) {
      width: 50%;
      padding-left: 15px;
      padding-right: 15px;
    }

    img {
      display: block;
      width: 100%;
      margin-bottom: 4px;
      border-radius: $border-radius;
    }
  }

  &__item-descr {
    font-size: $font-size--small;
    color: $gray;
  }

}
